import { db } from '@/lib/firebase';
import { Post, Category } from '@/types/blog';
import { collection, getDocs, query, where, orderBy, limit, Timestamp, doc, deleteDoc, updateDoc, getDoc } from 'firebase/firestore';

const COLLECTION_NAME = 'posts';

export const getPosts = async (category: Category = 'ALL'): Promise<Post[]> => {
    try {
        let q = query(
            collection(db, COLLECTION_NAME),
            where('status', '==', 'published'),
            where('publishedAt', '<=', Timestamp.now()),
            orderBy('publishedAt', 'desc'),
            limit(20)
        );

        if (category !== 'ALL') {
            q = query(
                collection(db, COLLECTION_NAME),
                where('status', '==', 'published'),
                where('category', '==', category),
                where('publishedAt', '<=', Timestamp.now()),
                orderBy('publishedAt', 'desc'),
                limit(20)
            );
        }

        const snapshot = await getDocs(q);

        return snapshot.docs.map(doc => ({
            id: doc.id,
            ...doc.data()
        } as Post));
    } catch (error) {
        console.error("Error fetching posts:", error);
        return [];
    }
};

export const getAdminPosts = async (): Promise<Post[]> => {
    try {
        const q = query(
            collection(db, COLLECTION_NAME),
            orderBy('createdAt', 'desc')
        );
        const snapshot = await getDocs(q);

        return snapshot.docs.map(doc => ({
            id: doc.id,
            ...doc.data()
        } as Post));
    } catch (error) {
        console.error("Error fetching admin posts:", error);
        return [];
    }
};

export const getPost = async (id: string): Promise<Post | null> => {
    try {
        const docRef = doc(db, COLLECTION_NAME, id);
        const docSnap = await getDoc(docRef);

        if (docSnap.exists()) {
            return { id: docSnap.id, ...docSnap.data() } as Post;
        } else {
            return null;
        }
    } catch (error) {
        console.error("Error fetching post:", error);
        return null;
    }
}

export const getPostBySlug = async (slug: string): Promise<Post | null> => {
    try {
        const q = query(
            collection(db, COLLECTION_NAME),
            where('slug', '==', slug),
            limit(1)
        );
        const snapshot = await getDocs(q);

        if (!snapshot.empty) {
            const doc = snapshot.docs[0];
            return { id: doc.id, ...doc.data() } as Post;
        } else {
            return null;
        }
    } catch (error) {
        console.error("Error fetching post by slug:", error);
        return null;
    }
}


export const deletePost = async (id: string) => {
    try {
        await deleteDoc(doc(db, COLLECTION_NAME, id));
        return true;
    } catch (error) {
        console.error("Error deleting post:", error);
        throw error;
    }
}

export const updatePost = async (id: string, data: Partial<Post>) => {
    try {
        // remove createdAt if it exists in data to avoid overwriting
        // eslint-disable-next-line @typescript-eslint/no-unused-vars
        const { createdAt, id: _id, ...updateData } = data;

        await updateDoc(doc(db, COLLECTION_NAME, id), {
            ...updateData,
            updatedAt: Timestamp.now()
        });
        return true;
    } catch (error) {
        console.error("Error updating post:", error);
        throw error;
    }
}
